import * as React from "react";
import * as _ from "lodash";

export interface TableByIdContextProps {
  [tableId: string]: {
    cellsElementsInTheLastColumn: HTMLElement[];
    resizerElementInTheLastColumn: HTMLDivElement;
    tableWidth: number;
    tableHeaderWidth: number;
  };
}

export interface BoxedExpressionGlobalContextProps {
  setContextByTableId: React.Dispatch<React.SetStateAction<TableByIdContextProps>>;
  contextByTableId: TableByIdContextProps;
}

export const BoxedExpressionGlobal = {
  context: React.createContext<Partial<BoxedExpressionGlobalContextProps>>({}),
  updateContextByTableId: (tableId: string, prop: string, value: unknown) => {
    return (prevTableByIdContext: TableByIdContextProps): TableByIdContextProps => {
      const contextForThisTable = prevTableByIdContext[tableId!] || {};
      const updatedProperty: { [prop: string]: unknown } = {};
      updatedProperty[prop] = value;
      _.assign(contextForThisTable, updatedProperty);
      const updatedTableByIdContext = { ...prevTableByIdContext };
      updatedTableByIdContext[tableId!] = contextForThisTable;
      return updatedTableByIdContext;
    };
  },
};
